import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import layout from '@/views/layout'
export default new Router({
  routes: [
    {
      path: '',
      component: layout,
      redirect: '/vuetest/ArticleList'
    },
    {
      path:"/vuetest",
      name:"vuetest",
      component:layout,
      children:[
        {
          path :'ArticleList',
          name:"ArticleList",
          component:()=>import('@/views/nodejs/articles') 
        },
        {
          path :'ArticleDetail',
          name:"ArticleDetail",
          component:()=>import('@/views/nodejs/details') 
        },
        {
          path :'todolist',
          name:"todolist",
          component:()=>import('@/views/todolist') 
        }
        ,
        {
          path :'calculate',
          name:"calculate",
          component:()=>import('@/views/calculate') 
        }
      ]
    }
  ]
})



